<template>
  <!--   swiperOption -->
  <swiper :options="swiperOption" ref="swiper">
    <!--      
:text inline  -->
    <div class="mine-scroll-pull-down" v-if="pullDown">
      <szh-loading :text="pullDownText" inline ref="pullDownLoading"></szh-loading>
    </div>
    <swiper-slide>
      <!--      -->
      <slot></slot>
    </swiper-slide>
    <!--     :text inline  -->
    <div class="mine-scroll-pull-up" v-if="pullUp">
      <szh-loading :text="pullUpText" inline ref="pullUpLoading"></szh-loading>
    </div>
    <!--    -->
    <div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
  </swiper>
</template>
<!-- -->
<script>
  // vue-awesome-swiper
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import SzhLoading from 'base/loading';
  import {
    // eslint-disable-next-line no-unused-vars
    PULL_DOWN_HEIGHT,
    PULL_DOWN_TEXT_INIT,
    PULL_DOWN_TEXT_START,
    // eslint-disable-next-line no-unused-vars
    PULL_DOWN_TEXT_ING,
    // eslint-disable-next-line no-unused-vars
    PULL_DOWN_TEXT_END,
    // eslint-disable-next-line no-unused-vars
    PULL_UP_HEIGHT,
    // eslint-disable-next-line no-unused-vars
    PULL_UP_TEXT_INIT,
    // eslint-disable-next-line no-unused-vars
    PULL_UP_TEXT_START,
    // eslint-disable-next-line no-unused-vars
    PULL_UP_TEXT_ING,
    // eslint-disable-next-line no-unused-vars
    PULL_UP_TEXT_END
  } from './config';

  export default {
    name: 'SzhScrool',
    components: {
      swiper,
      swiperSlide,
      SzhLoading
    },
    props: {
      scrollbar: {
        type: Boolean,
        default: true
      },
      watchedData: {
        type: [Array, Object]
      },
      pullDown: {
        type: Boolean,
        default: true
      },
      pullUp: {
        type: Boolean,
        default: true
      }
    },
    created() {
      this.init();
    },
    // swiperOption
    // data() {
    //   return {
    //     pulling: false, // 
    //     pullDownText: ',',
    //     pullUpText: '',
    //     swiperOption: {
    //       direction: 'vertical',
    //       slidesPerView: 'auto',
    //       freeMode: true,
    //       setWrapperSize: true,
    //       scrollbar: {
    //         el: this.scrollbar ? '.swiper-scrollbar' : null,
    //         hide: true
    //       },
    //       // scroll
    //       on: {
    //         sliderMove: this.scroll,
    //         touchEnd: this.touchEnd // 
    //       }
    //     }
    //   };
    // },
    watch: {
      watchedData() {
        this.update();
      }
    },
    methods: {
      // swiperOption
      init() {
        this.pulling = false; // 
        this.pullDownText = ',';
        this.pullUpText = '';
        this.swiperOption = {
          direction: 'vertical',
          slidesPerView: 'auto',
          freeMode: true,
          setWrapperSize: true,
          scrollbar: {
            el: this.scrollbar ? '.swiper-scrollbar' : null,
            hide: true
          },
          // scroll
          on: {
            sliderMove: this.scroll,
            touchEnd: this.touchEnd, // 
            transitionEnd: this.scrollEnd // 
          }
        };
      },
      // swiperupdate
      update() {
        this.$refs.swiper && this.$refs.swiper.swiper.update();
      },
      // 
      scrollToTop(speed, runCallback) {
        this.$refs.swiper && this.$refs.swiper.swiper.slideTo(0, speed, runCallback);
      },
      // swiperswiper,
      scroll() {
        const swiper = this.$refs.swiper.swiper;
        // console.log(swiper.translate);

        this.$emit('scroll', swiper.translate, this.$refs.swiper.swiper);
        // 
        if (this.pulling) {
          return;
        }
        ;
        if (swiper.translate > 0) { // 
          if (!this.pullDown) {
            return;
          }
          if (swiper.translate > PULL_DOWN_HEIGHT) {
            this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_START);
          } else {
            this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_INIT);
          }
          ;
        } else if (swiper.isEnd) { // 
          if (!this.pullUp) {
            return;
          }
          ;
          // console.log(Math.abs(swiper.translate));
          // console.log(swiper.height);
          // console.log(swiper);
          // console.log(parseInt(swiper.$warpperEl.css('height')));
          // console.log(Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT);
          // console.log(swiper.height);
          // console.log(parseInt(swiper.$wrapperEl.css('height')));
          const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > parseInt(swiper.$wrapperEl.css('height'));
          // console.log(isPullUp);
          if (isPullUp) {
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_START);
          } else {
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_INIT);
          }
        }
        ;
      },
      // 
      scrollEnd() {
        this.$emit('scroll-end', this.$refs.swiper.swiper.translate, this.$refs.swiper.swiper, this.pulling);
      },
      // swiperswiper,
      touchEnd() {
        const swiper = this.$refs.swiper.swiper;
        if (this.pulling) {
          return;
        }
        if (swiper.translate > PULL_DOWN_HEIGHT) {
          if (!this.pullDown) {
            return;
          }

          this.pulling = true; // 
          swiper.allowTouchMove = false; // 
          swiper.setTransition(swiper.params.speed); // swiper
          swiper.setTranslate(PULL_DOWN_HEIGHT); // 
          swiper.params.virtualTranslate = true; // 
          this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_ING); // 
          this.$emit('pull-down', this.pullDownEnd); // pull-down
        } else if (swiper.isEnd) {
          const totalHeight = parseInt(swiper.$wrapperEl.css('height'));
          const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > parseInt(swiper.$wrapperEl.css('height'));

          if (isPullUp) {
            if (!this.pullUp) {
              return;
            }
            this.pulling = true;
            swiper.allowTouchMove = false; // 
            swiper.setTransition(swiper.params.speed); // swiper
            swiper.setTranslate(-(totalHeight + PULL_UP_HEIGHT - swiper.height)); // 
            swiper.params.virtualTranslate = true; // 
            this.$refs.pullDownLoading.setText(PULL_UP_TEXT_ING); // 
            this.$emit('pull-up', this.pullUpEnd); // pull-down
            ;
          }
          ;
        }
        ;
      },
      // swiperswiper,
      pullDownEnd() {
        this.pulling = false; // 
        const swiper = this.$refs.swiper.swiper;
        swiper.allowTouchMove = true; // 
        swiper.setTransition(swiper.params.speed); // swiper
        swiper.params.virtualTranslate = false; // 
        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END); // 
        swiper.setTranslate(0); // 
        setTimeout(() => {
          this.$emit('pull-down-transition-end');
        }, swiper.params.speed);
      },
      pullUpEnd() {
        this.pulling = false; // 
        const swiper = this.$refs.swiper.swiper;
        swiper.allowTouchMove = true; // 
        swiper.setTransition(swiper.params.speed); // swiper
        swiper.params.virtualTranslate = false; // 
        // this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END); // 
        // swiper.setTranslate(0);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .swiper-container {
    overflow: hidden;
    width: 100%;
    height: 100%;
  }

  /**/
  .swiper-slide {
    height: auto;
  }

  .mine {
    &-scroll-pull-down {
      position: absolute;
      left: 0;
      bottom: 100%;
      width: 100%;
      height: 80px;
    }

    &-scroll-pull-up {
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%;
      height: 20px;
    }

  }
</style>



