// axios\jsonp\config
import axios from 'axios';
import {SUCCEED_CODE, SETTIMEOUT, HOME_RECOMMEND_PAGE_SIZE, jsonpOptions} from 'api/config';
import jsonp from '../assets/js/jsonp';
// 
const shuffle = (arr) => {
  const arrLength = arr.length;
  let i = arrLength;
  let randomIndex;
  while (i--) { // 
    if (i !== (randomIndex = Math.floor(Math.random() * arrLength))) {
      [arr[i], arr[randomIndex]] = [arr[randomIndex], arr[i]];
    }
  }
  return arr;
};
// 
export const getHomeSlider = () => {
  return axios.get('https://www.imooc.com/api/home/slider', {timeout: SETTIMEOUT}).then(res => {
    if (res.data.code === SUCCEED_CODE) {
      let randomSliders = res.data.slider;
      // console.log(typeof (randomSliders)); //  =>,10
      const oneSlider = shuffle([randomSliders[Math.floor(Math.random() * randomSliders.length)]]);
      randomSliders = randomSliders.filter(() => Math.random() >= 0.5); // 
      if (randomSliders.length === 0) {
        randomSliders = oneSlider;
      }
      // ,res.dataslider
      // return res.data.slider;
      console.log(randomSliders);
      return randomSliders;
    }
    throw new Error('');
  }).catch(err => {
    if (err) {
      console.log(err);
    }
    return [
      {
        linkUrl: 'https://www.imooc.com',
        picUrl: require('assets/img/404.png')
      }
    ];
  }).then(data => {
    return new Promise(resolve => {
      setTimeout(() => { // 
        resolve(data);
      }, 1000);
    });
  });
};
// jsonp
export const getHomeRecommend = (page = 1, psize = HOME_RECOMMEND_PAGE_SIZE) => {
  const url = 'https://ju.taobao.com/json/tg/ajaxGetItemsV2.json';
  const params = {
    page,
    psize,
    type: 0,
    frontCatId: ''
  };

  return jsonp(url, params, jsonpOptions).then(res => {
    if (res.code === '200') {
      return res;
    }
  });
};



